* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
h2 {
  margin: 0;
}
ul {
  list-style: none;
}
.country-title-box {
  display: flex;
  align-items: baseline;
}
.country-flag {
  margin-right: 5px;
}
p {
  font-weight: 700;
}
span {
  font-weight: 400;
}

.languages-item:not(:last-child):after {
content: ',';
}
.country-title {
  font-weight: 400;
}